'use client'

import React, { useEffect, useState } from 'react'
import { UserCheck } from "@phosphor-icons/react";
import Grid from '@mui/material/Grid2';
import useTheme from '@mui/material/styles/useTheme';
import { Box, TextField, Typography } from '@mui/material';

import { BaseButton } from '@/components/Button';
import Loading from './Loading';

const RegisterForm = () => {
    const liffId = process.env.NEXT_PUBLIC_LIFF_ID || "";
    const [isIintLine, setIintLine] = useState(false);
    useEffect(() => {
        setLoading(true);
        const initializeLiff = async () => {
            try {
                await new Promise((resolve) => setTimeout(resolve, 3000));
                const liff = (await import('@line/liff')).default;
                await liff.init({ liffId });
                setIintLine(true);
                setLoading(false);
            } catch (error) {
                if (error instanceof Error) {
                    console.error('liff init error', error.message);
                }
            }
        };
        initializeLiff();
    }, []);

    const theme = useTheme();
    const u = theme.palette.background.default;
    const [isLoading, setLoading] = React.useState(false);

    const regisger = async () => {
        setLoading(true);
        await new Promise((resolve) => setTimeout(resolve, 3000));
        setLoading(false);
    }



    return (
        <Box>
            <Loading open={isLoading} />
            {isIintLine ? (
                <Grid container spacing={1}
                    alignItems="center"
                    justifyContent="center"
                    display="flex"
                    height="100vh"
                    sx={{ backgroundColor: u }}>

                    <Grid size={{ xs: 12, md: 6 }} sx={{ px: { xs: 5, md: 0 } }}>
                        <Box sx={{ py: 5, bgcolor: "white", px: 5, borderRadius: 4 }} >
                            <Box sx={{ py: 1.5 }} alignItems="center" justifyContent="center" textAlign="center">
                                <Typography sx={{ fontSize: '1rem' }}>ลงทะเบียน</Typography>
                            </Box>

                            <TextField
                                id="standard-basic"
                                placeholder='เลขประจำตัวผู้เสียภาษี'
                                fullWidth
                                slotProps={
                                    {
                                        input: {
                                            endAdornment: <UserCheck size={30} />
                                        }
                                    }
                                }
                            />
                            <Box sx={{ py: 1.5 }}  >
                                <BaseButton onTap={regisger} text='ลงทะเบียน' color='primary' variant='contained' fullWidth />
                            </Box>

                        </Box>
                    </Grid>
                </Grid >
            ) : null
            }

        </Box>
    );


}

export default RegisterForm
